<template>
  <div class="app-market" v-loading="loading">
    <div class="search-area">
      <el-button class="back-btn" size="mini" @click="$router.go(-2)"
        ><i class="el-icon-arrow-left"></i>返回</el-button
      >
      <el-input
        class="search-input"
        v-model="keyword"
        placeholder="输入分类或应用名称搜索"
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="getAppList"
        ></el-button>
      </el-input>
      <el-button type="primary" @click="goApplication">成为开发者</el-button>
    </div>
    <app-classify v-model="categoryCode" @change="getAppList" />
    <div class="list">
      <div
        class="list-item"
        v-for="item in appList"
        :key="item.appId"
        @click="visitItem(item)"
        v-ripple
      >
        <img :src="getImageUrl(item.icon, false, item.appId)" alt="" />
        <div class="list-item-info">
          <span class="app-name">{{ item.appName }}</span>
          <div class="secondary">
            <div class="left">
              <span class="app-category">{{ item.categoryName }}</span>
            </div>
            <template>
              <el-button
                v-if="item.userInstallStatus === 1"
                class="opt-btn"
                size="mini"
                @click.stop="installItemApp(item)"
                >安装</el-button
              >

              <el-button
                v-else-if="item.userInstallStatus === 2"
                class="opt-btn"
                type="primary"
                size="mini"
                @click.stop="useApp(item)"
                >访问</el-button
              >
              <el-button
                v-else-if="item.userInstallStatus === 3"
                class="opt-btn"
                type="primary"
                size="mini"
                @click.stop="upgrade(item.appId)"
                >升级</el-button
              >
            </template>
          </div>
          <div class="description">{{ item.description || '该开发者很懒，没有填写应用描述' }}</div>
        </div>
      </div>
    </div>
    <div style="text-align:right; padding: 10px 20px 0 0">
      <pagination
        :page="searchForm.page"
        :limit="searchForm.pageSize"
        :total="total"
        :page-sizes="pageSizes"
        @pagination="handlePageChange"
      />
    </div>
  </div>
</template>

<script>
//func
import * as API from '@/api/applicationMarket'
import { getImageUrl } from '@/utils'
import appOperate from '../mixins/appOperate'
import appClassify from './components/appClassify'
import { authKey } from '@/router'
import breadcrumbControl from '@/mixins/breadcrumb-control'
import Pagination from '@/components/Pagination/index.vue'

export default {
  components: {
    appClassify,
    Pagination
  },
  mixins: [appOperate, breadcrumbControl],
  props: {},
  data() {
    return {
      keyword: '',
      categoryCode: '',
      loading: false,
      appList: [],
      total: 0,
      getImageUrl,
      pageSizes: [30, 50, 100, 200],
      searchForm: {
        page: 1,
        pageSize: 30
      },
    }
  },
  created() {
    this.getAppList()
  },
  mounted() {},
  methods: {
    handlePageChange({ page, size }) {
      this.searchForm.page = page
      this.searchForm.pageSize = size
      this.getAppList()
    },
    goApplication() {
      this.$router.push({
        name: authKey.Application,
      })
    },
    async upgrade(appId) {
      await this.appUpgrade(appId)
      this.getAppList()
    },
    async installItemApp(item) {
      await this.appInstall(item)
      this.getAppList()
    },
    getParams() {
      return {
        condition: {
          categoryCode: this.categoryCode,
          keyword: this.keyword,
        },
        pagination: {
          pagenum: this.searchForm.page,
          pagesize: this.searchForm.pageSize,
          sort: {
            name: '',
            type: '',
          },
        },
      }
    },
    // 获取应用列表
    getAppList() {
      this.loading = true
      API.appsPage(this.getParams())
        .then((res) => {
          const { records, rowCount } = res.data
          this.appList = records
          this.total = rowCount
        })
        .finally(() => {
          this.loading = false
        })
    },
    visitItem(item) {
      this.$router.push({
        name: authKey.appMarketDetail,
        query: { id: item.appId },
      })
    },
  },

  computed: {},

  watch: {},
}
</script>
