<template>
  <div class="app-classify" :class="{ 'expend-status': expend }">
    <span
      :class="{ active: value === item.code }"
      v-for="item in list"
      :key="item.code"
      @click="$emit('change', item.code)"
    >
      {{ item.name }}
    </span>
    <template v-if="list.length >= 15">
      <i
        v-if="expend"
        class="expend el-icon-arrow-up"
        @click="toggleExpend"
      ></i>
      <i v-else class="expend el-icon-arrow-down" @click="toggleExpend"></i>
    </template>
  </div>
</template>

<script>
import * as API from '@/api/applicationMarket.js'
export default {
  model: {
    prop: 'value',
    event: 'change',
  },
  components: {},
  props: {
    value: {
      type: [String, Number],
      require: true,
    },
    placeholder: {
      type: String,
      defalut: '',
    },
  },
  data() {
    return {
      list: [],
      expend: false,
    }
  },
  created() {
    this.getList()
  },
  mounted() {},
  methods: {
    // 获取分类列表
    getList() {
      API.appCategoryList().then((res) => {
        const { data } = res
        this.list = [{ name: '全部应用', code: '' }].concat(data)
      })
    },
    toggleExpend() {
      this.expend = !this.expend
    },
  },

  computed: {},

  watch: {},
}
</script>
<style lang="less" scoped>
.app-classify {
  padding: 10px 18px;
  width: 940px;
  height: 40px;
  overflow-y: hidden;
  position: relative;
  span {
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #333333;
    cursor: pointer;
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 2px 5px;
    border-radius: 4px;
    &:hover {
      background-color: #66b1ff;
      color: white;
    }
    &.active {
      color: #3f92fe;
      background-color: transparent;
    }
  }
  .expend {
    cursor: pointer;
    font-size: 25px;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    margin-top: 3px;
    margin-left: -2px;
    &:hover {
      background: #eee;
      border-radius: 5px;
    }
  }
  &.expend-status {
    height: auto;
    .expend {
      position: relative;
      transform: none;
      top: 3px;
      margin: 0;
    }
  }
}
</style>
